@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto);
.dist {
    font-family: Arial, Helvetica, sans-serif ;
    font-size: 14pt !important;
    color: rgb(60, 60, 60);
}

.paper {
    background: linear-gradient(90deg, rgba(207,207,207,1) 0%, rgba(226,223,223,1) 35%, rgba(232,232,232,1) 100%);
}

a, a:hover {
    font-family: Arial, Helvetica, sans-serif;
    color: black;
    font-size: 14pt;
}

.address {
    margin-top: 15px !important;
}

.brandName {
    font-family: Arial, Helvetica, sans-serif;
    color: black;
    font-size: 18pt;
    font-weight:600;
}
.bookmarkButton {
    border: none;
    background-color: transparent;
}
.container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
  
.row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #9b9b9b;
  width: 900px;
}
  
.gasStation {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.gasStation h1 {
  font-size: 20px;
  width: 150px;
}

.gasStation img {
  height: 30px;
  width: 80px;
  margin-right: 35px;
}

.gasStation-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.gasStation-data-price {
  width: 110px;
}

.gasStation-data-address {
  width: 300px;
}

.gasStation-data-coordinates {
  width: 150px;
}

.gasStation-data-distance {
  width: 80px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}
.emptyMessage {
    background: linear-gradient(90deg, rgba(207,207,207,1) 0%, rgba(226,223,223,1) 35%, rgba(232,232,232,1) 100%);
    margin-top: 30px;
    padding: 30px 150px 30px 150px;
    border-radius: 4px;
    color: black;
    font-size: 14pt;
    text-align: center;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}

.emptyMessage > img {
    margin: 20px 0 20px 0;
    width: 160px;
    height: 160px;
}
.container-search-configurator {
    background: linear-gradient(90deg, rgb(220, 220, 220) 0%, rgba(226,223,223,1) 35%, rgba(232,232,232,1) 100%);
    width: 600px;
}
.headline-filter {
    margin-top: 30px;
}
.container-gasstations, .container-search {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin: 30px 0 50px 0;
  padding: 0;
}

.headline {
  margin-bottom: 36px;
}
.loginMessage {
    padding: 50px;
}

.loginMessage > a {
    color: rgb(255, 123, 0);
}
@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
    }
  
    .Login form {
      margin: 0 auto;
      max-width: 320px;
    }

    .Login p {
        padding-top: 10px;
    }

    .Login a {
        color: rgb(255, 123, 0);
    }
  }

body {
  font-family: 'Roboto', sans-serif;
  background: #fff; /* fallback for old browsers */
}

* {
  margin: 0;
  padding: 0;
}

i {
  margin-right: 10px;
}

.navbar-logo {
  padding: 15px;
  color: #fff;
  pointer-events: none;
}

a.nav-link.active {
  background-color: #1876d1;
}

.navbar-mainbg {
  background-color: #000000;
  padding: 0px;
}

#navbarSupportedContent {
  overflow: hidden;
  position: relative;
}

#navbarSupportedContent ul {
  padding: 0px;
  margin: 0px;
}

#navbarSupportedContent ul li a i {
  margin-right: 10px;
}

#navbarSupportedContent li {
  list-style-type: none;
  float: left;
}

#navbarSupportedContent ul li a {
  color: rgba(255, 255, 255, 1.0);
  text-decoration: none;
  font-size: 15px;
  display: block;
  padding: 20px 20px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
}

#navbarSupportedContent > ul > li.active > a {
  color: #000000;
  background-color: transparent;
  transition: all 0.7s;
}

#navbarSupportedContent a:not(:only-child):after {
  content: '\f105';
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 14px;
  font-family: 'Font Awesome 5 Free';
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  transition: 0.5s;
}

#navbarSupportedContent .active > a:not(:only-child):after {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.hori-selector {
  display: inline-block;
  position: absolute;
  height: 100%;
  top: 0px;
  left: 0px;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background-color: rgb(255, 255, 255);
  margin-top: 15px; 
}


.hori-selector .right,
.hori-selector .left {
  position: absolute;
  width: 100px;
  height: 25px;
  background-color: rgb(0, 0, 0);
  bottom: 5px;
}

.hori-selector .right {
  position: absolute;
  right: -25px;
}

.hori-selector .left {
  position: absolute;
  left: -25px;
}

.hori-selector .right:before,
.hori-selector .left:before {
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #000000;
}

.hori-selector .right:before {
  bottom: 0;
  right: -25px;
}

.hori-selector .left:before {
  bottom: 0;
  left: -25px;
}

@media (max-width: 991px) {
  #navbarSupportedContent ul li a {
    padding: 12px 30px;
  }
  .hori-selector {
    margin-top: 0px;
    margin-left: 10px;
    border-radius: 0;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }
  .hori-selector .left,
  .hori-selector .right {
    right: 10px;
  }
  .hori-selector .left {
    top: -25px;
    left: auto;
  }
  .hori-selector .right {
    bottom: -25px;
  }
  .hori-selector .left:before {
    left: -25px;
    top: -25px;
  }
  .hori-selector .right:before {
    bottom: -25px;
    left: -25px;
  }
}

